import { createContext, useMemo, useState } from "react";

export const userDataContext = createContext();

const UserData = (props) => {

    const [isSignedIn, setIsSignedIn] = useState(false);
    const [name, setName] = useState('');
    const [userId, setUserId] = useState(' ');
    const [wordSetData, setWordSetData] = useState([]);

    const uesr = {
        isSignedIn: isSignedIn,
        name: name,
        userId: userId,
        wordSetData: wordSetData,

        setIsSignedIn: (bool) => {
            setIsSignedIn(bool);
        },

        setName: (name) => {
            setName(name);
        },

        setUserId: (id) => {
            setUserId(id);
        },

        setWordSetData: (data) => {
            setWordSetData(data);
        },

        addWordSetData: (data) => {
            setWordSetData(...wordSetData, data);
        },
    }

    //useMemo :  
    const value = useMemo(() => (uesr), [uesr]); //record      {} 

    return <userDataContext.Provider value={value}>{props.children}</userDataContext.Provider>;
}

export default UserData;